"use client";
import { useState, useEffect, useRef } from "react";
import { useTranslations } from "next-intl";
import { motion, AnimatePresence } from "framer-motion"; // –î–æ–±–∞–≤–ª—è–µ–º
import styles from "@/app/styles/componentStyles/header/headerNav.module.css";

export function HeaderNav() {
  const t = useTranslations("translation.nav");
  const [isOpen, setIsOpen] = useState(false);

  const menuRef = useRef<HTMLUListElement | null>(null);
  const burgerRef = useRef<HTMLDivElement | null>(null);



  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (
        menuRef.current &&
        !menuRef.current.contains(event.target as Node) &&
        burgerRef.current &&
        !burgerRef.current.contains(event.target as Node)
      ) {
        setIsOpen(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  return (
    <nav className={styles.nav}>
      {/* –ë—É—Ä–≥–µ—Ä-–∏–∫–æ–Ω–∫–∞ */}

      <div
        className={styles.burger}
        onClick={() => setIsOpen(!isOpen)}
        ref={burgerRef}
      >
        <span></span>
        <span></span>
        <span></span>
      </div> 

      {/* –î–µ—Å–∫—Ç–æ–ø-–º–µ–Ω—é */}
      <motion.ul
        className={styles.list}
        initial={{ opacity: 0, y: -10 }} // –ú–µ–Ω—é –ø–æ—è–≤–ª—è–µ—Ç—Å—è —Å–≤–µ—Ä—Ö—É
        animate={{ opacity: 1, y: 0 }} // –ê–Ω–∏–º–∞—Ü–∏—è –ø–æ—è–≤–ª–µ–Ω–∏—è
        transition={{ duration: 0.5, ease: "easeOut" }}
      >
        {t
          .raw("items")
          .map(
            (
              { href, label, active }: { href: string; label: string; active?: boolean },
              index: number,
            ) => (
              <motion.li
                key={href}
                className={styles.item}
                initial={{ opacity: 0, y: -10 }} // –ö–∞–∂–¥—ã–π –ø—É–Ω–∫—Ç –ø–∞–¥–∞–µ—Ç —Å–≤–µ—Ä—Ö—É
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: index * 0.1 }} // –ó–∞–¥–µ—Ä–∂–∫–∞ –¥–ª—è –ø–ª–∞–≤–Ω–æ—Å—Ç–∏
                whileHover={{ scale: 1.1 }} // –≠—Ñ—Ñ–µ–∫—Ç —É–≤–µ–ª–∏—á–µ–Ω–∏—è –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏
              >
                <a className={`${styles.link} ${active ? styles.active : ""}`} href={href}>
                  {label}
                </a>
              </motion.li>
            ),
          )}
      </motion.ul>

      <AnimatePresence>
        {isOpen && (
          <motion.ul
            className={styles.mobileMenu}
            ref={menuRef}
            initial={{ scale: 0.9, opacity: 0 }} // –ù–∞—á–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ
            animate={{ scale: 1, opacity: 1 }} // –ê–Ω–∏–º–∞—Ü–∏—è –ø–æ—è–≤–ª–µ–Ω–∏—è
            exit={{ scale: 0.9, opacity: 0 }} // –ê–Ω–∏–º–∞—Ü–∏—è –∏—Å—á–µ–∑–Ω–æ–≤–µ–Ω–∏—è
            transition={{ duration: 0.3, ease: "easeInOut" }}
          >
            {t
              .raw("items")
              .map(
                (
                  { href, label, active }: { href: string; label: string; active?: boolean },
                  index: number,
                ) => (
                  <motion.li
                    key={href}
                    className={styles.item}
                    initial={{ opacity: 0, y: -10 }} // –≠–ª–µ–º–µ–Ω—Ç—ã –ø–∞–¥–∞—é—Ç —Å–≤–µ—Ä—Ö—É
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: index * 0.1 }} // –ó–∞–¥–µ—Ä–∂–∫–∞ –º–µ–∂–¥—É —ç–ª–µ–º–µ–Ω—Ç–∞–º–∏
                  >
                    <a
                      className={`${styles.link} ${active ? styles.active : ""}`}
                      href={href}
                      onClick={() => setIsOpen(false)}
                    >
                      {label}
                    </a>
                  </motion.li>
                ),
              )}
          </motion.ul>
        )}
      </AnimatePresence>
    </nav>
  );
}
