"use client";
import styles from "@/app/styles/componentStyles/main/worksSection.module.css";
import { useTranslations } from "next-intl";
import Link from "next/link";
import { motion } from "framer-motion";
import { letterVariants, cardVariants, techVariants } from "@/utils/motionVariants";

function WorksSection() {
  const t = useTranslations("translation.worksSection");
  const projects = useTranslations("translation");

  return (
    <section id="works" className={styles.section}>
      <div className={styles.container}>
        {/* –ê–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—ã–µ –∑–∞–≥–æ–ª–æ–≤–∫–∏ */}
        <motion.h2
          className={styles.header}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true }}
        >
          {t("title")
            .split("")
            .map((char, index) => (
              <motion.span key={index} custom={index} variants={letterVariants}>
                {char}
              </motion.span>
            ))}
        </motion.h2>

        <motion.h3
          className={styles.projects}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true }}
        >
          {t("projects")
            .split("")
            .map((char, index) => (
              <motion.span key={index} custom={index} variants={letterVariants}>
                {char}
              </motion.span>
            ))}
        </motion.h3>
      </div>

      {/* –ê–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—ã–µ –∫–∞—Ä—Ç–æ—á–∫–∏ –ø—Ä–æ–µ–∫—Ç–æ–≤ */}
      <motion.div
        className={styles.worksGrid}
        initial="hidden"
        whileInView="visible"
        viewport={{ once: true }}
      >
        {projects.raw("projects").map((project: any, index: number) => (
          <motion.div
            key={index}
            className={styles.card}
            custom={index}
            variants={cardVariants}
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true }}
          >
            <img className={styles.image} src={project.image} alt={project.name} />
            <h3 className={styles.title}>{project.name}</h3>
            <p className={styles.description}>{project.description}</p>

            {/* –ê–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—ã–π —Å—Ç–µ–∫ —Ç–µ—Ö–Ω–æ–ª–æ–≥–∏–π */}
            <div className={styles.stack}>
              {project.stack.map((tech: string, i: number) => (
                <motion.span
                  key={tech}
                  className={styles.tech}
                  custom={i}
                  variants={techVariants}
                  initial="hidden"
                  whileInView="visible"
                  viewport={{ once: true }}
                >
                  {tech}
                  {i < project.stack.length - 1 && ", "}
                </motion.span>
              ))}
            </div>

            <Link
              target="_blank"
              rel="noopener noreferrer"
              href={project.life}
              className={styles.viewInfo}
            >
              {t("demo")}
            </Link>
          </motion.div>
        ))}
      </motion.div>
    </section>
  );
}

export default WorksSection;
